import { Grid, TextField } from "@material-ui/core";
import React, { useState } from "react";
import CustomButton from "../../components/Button/CustomButton";
import "./contact.css";

const Contact = () => {
  const [userMessage, setUserMessage] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
  });
  const { name, email, phone, message } = userMessage;
  // const [Message, setMessage] = useState("");

  const handleChanged = (e) => {
    const { name, value } = e.target;
    setUserMessage({ ...userMessage, [name]: value });
  };

  console.log("object===========>", message);

  return (
    // <div className="name">
    <Grid
      container
      spacing={5}
      className="section text_pb text_pt"
      style={{ padding: 50 }}
    >
      {/* Contact Form */}
      <Grid item xs={12} lg={12}>
        <Grid container>
          <Grid item className="section_title text_mb">
            <span></span>
            <h6 className="section_title_text">Contact Me</h6>
          </Grid>
          <Grid item xs={12}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  name="name"
                  label="Name"
                  onChange={handleChanged}
                  value={name}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  name="email"
                  label="E-Mail"
                  onChange={handleChanged}
                  value={email}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  name="phone"
                  label="Phone"
                  onChange={handleChanged}
                  value={phone}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  name="message"
                  label="Message"
                  multiline
                  value={message}
                  onChange={handleChanged}
                  // onChange={(e) => {
                  //   setMessage(e.target.value);
                  //   console.log("object", Message);
                  // }}
                />
              </Grid>
              <Grid item xs={12} style={{ textAlign: "center" }}>
                <CustomButton className="button" text="Submit" />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
    // </div>
  );
};

export default Contact;
